import interfaceurl from '@/utils/interface.js'
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	computed: mapState(['goodsDetail']),
	data() {
		return {
			id: '',
			confirmModal: false,
			indicatorDots: false,
			autoplay: true,
			interval: 2000,
			duration: 500,
			detail: {
				goods_name: '红牡丹抽色润颜霜 均匀亮白十全十美套盒'
			},
			shopNum: 1,
			type: 1,
			bannerList: ['/static/banner.png'],
			shopItem: {},
			invitation_code: '',
			userInfo: {},
			is_cash_back: '0',
			//动态参数
			skuPropList: [{}, {}, {}, {}, {}], //商品属性
			propsCheck: {}, //属性选择
			//goodsDetail: {}, //商品详情，暂时
			cartOrOrder: true, //加入购物车：true,下单：false
			inventoryCount: 0 //各组sku的库存量
		}
	},
	onShareAppMessage(res) {
		return {
			title: '一米一粉',
			path: '/pages/shop-detail/shop-detail?id=' + this.id + '&code=' + this.invitation_code,
			imageUrl: '/static/share-banner.png'
		}
	},
	onLoad(options) {
		let item = this.deepCopy(this.goodsDetail)
		item.detailInfo = item.detailInfo.replace(/<img/gi, '<img width="100%!important" ')
		this.$store.commit('setGoodsDetail', item)
		this.getData()
		//this.getGoodsDetail()
	},
	onShow() {
		const value = wx.getStorageSync('userInfo')
		if (value) {
			this.userInfo = value
		}
	},
	methods: {
		touch() {},
		chooseProps(index, i) {
			this.propsCheck[index] = i
			//更改库存
			let element = this.judgeCode()
			this.inventoryCount = element ? element.inventoryCount : 0
		},
		getGoodsDetail() {
			let that = this;
			let params = {
				spuId: 115199
			}
			interfaceurl.checkAuth(interfaceurl.goodsDetail, params, false).then((res) => {
				that.goodsDetail = res.data
				that.data()
				//抽取商品的规格属性
				// let props = {}
				// let propsCheck = {} //属性选择
				// for(var item of that.goodsDetail.skuList) {
				// 	for(var subItem of item.skuPropertyList) {
				// 		if(!props[subItem.name]) {
				// 			props[subItem.name] = []
				// 			propsCheck[subItem.name]= 0
				// 		}
				// 		if(props[subItem.name].indexOf(subItem.val) <= -1) {
				// 			props[subItem.name].push(subItem.val)
				// 		}
				// 	}
				// }
				// that.skuPropList = props
				// that.propsCheck = propsCheck
				// console.log(props)
				// console.log(propsCheck)
				// let element = that.judgeCode()
				// that.inventoryCount = element ? element.inventoryCount : 0
			});
		},
		//梳理商品详情数据
		getData() {
			let that = this
			//抽取商品的规格属性
			let props = {}
			let propsCheck = {} //属性选择
			for(var item of that.goodsDetail.skuList) {
				for(var subItem of item.skuPropertyList) {
					if(!props[subItem.name]) {
						props[subItem.name] = []
						propsCheck[subItem.name]= 0
					}
					if(props[subItem.name].indexOf(subItem.val) <= -1) {
						props[subItem.name].push(subItem.val)
					}
				}
			}
			that.skuPropList = props
			that.propsCheck = propsCheck
			console.log(props)
			console.log(propsCheck)
			let element = that.judgeCode()
			that.inventoryCount = element ? element.inventoryCount : 0
		},
		deepCopy(obj) {
			var result = Array.isArray(obj) ? [] : {};
			for (var key in obj) {
				if (obj.hasOwnProperty(key)) {
					if (typeof obj[key] === 'object' && obj[key] !== null) {
						result[key] = this.deepCopy(obj[key]); //递归复制
					} else {
						result[key] = obj[key];
					}
				}
			}
			return result;
		},
		//判断用户选择的属性规格是否存在，存在则返回对应的code，否则提示无库存
		judgeCode() {
			let check = {}
			let flag = 0
			for(let key in this.propsCheck){
				check[key] = this.skuPropList[key][this.propsCheck[key]]
				//console.log(check)
			}
			for(var item of this.goodsDetail.skuList) {
				for(var subItem of item.skuPropertyList) {
					if(check[subItem.name] == subItem.val) {
						flag ++;
					}
				}
				if(flag == item.skuPropertyList.length) {
					//console.log('用户选择存在',item)
					//如果规格值等于一组中的所有值，则用户所选规格存在，返回该组sku对应的code
					return item;
				}
				flag = 0
			}
			//console.log('用户选择不存在')
			return null
		},
		addCart(code) {
			let that = this;
			let params = {
				code: code, //商品sku
				quantity: this.shopNum
			}
			interfaceurl.checkAuth(interfaceurl.cartCreate, params, false).then((res) => {
				uni.showToast({
					title: '购物车添加成功',
					icon: 'success',
					duration: 2000
				});
			});
		},
		getUserInfo(e) {
			console.log(e)
			this.userInfo = e.detail.userInfo
			if (e.detail.rawData) {
				this.userInfo = JSON.parse(e.detail.rawData)
			}
			this.saveUserInfo()
		},
		saveUserInfo() {
			apiUserInfoSave({
					nickName: this.userInfo.nickName,
					gender: this.userInfo.gender,
					avatarUrl: this.userInfo.avatarUrl
				})
				.then((res) => {
					if (res.code == 0) {
						wx.setStorageSync('userInfo', this.userInfo)
					}
				})
		},
		getUserCode() {
			apiUserInfo()
				.then((res) => {
					if (res.code == 0) {
						this.invitation_code = res.data.invitation_code
					}
				})
		},
		reduceNum() {
			if (this.shopNum <= 1) {
				this.shopNum = 1
			}
			this.shopNum--
		},
		addNum() {
			this.shopNum++
		},
		handlePay(type) {
			if (type == 1 && this.is_cash_back == 1) {
				uni.showToast({
					title: '全返商品请直接购买',
					icon: 'none',
					duration: 2000
				});
				return
			}
			this.type = type
			this.confirmModal = true
		},
		shoppingPage() {
			uni.switchTab({
				url: '/pages/shopping/shopping'
			});
		},
		handleConfirm() {
			let item = this.judgeCode()
			if(!item) {
				uni.showToast({
					title: '库存数量不足',
					icon: 'none',
					duration: 2000
				});
				return;
			}
			if(this.cartOrOrder) {
				this.addCart(item.code)
			} else {
				this.confrimOrderPage()
			}
			this.confirmModal = false
			// if (this.type == 1) {
			// 	apiShoppingAdd({
			// 			goods_id: this.id,
			// 			buy_number: this.shopNum
			// 		})
			// 		.then((res) => {
			// 			if (res.code == 0) {
			// 				uni.showToast({
			// 					title: '已加入购物车',
			// 					icon: 'none',
			// 					duration: 2000
			// 				});
			// 			}
			// 		})
			// } else {
			// 	this.confrimOrderPage()
			// }
		},
		getShopDetail() {
			apiShopDetail({
					goods_id: this.id
				})
				.then((res) => {
					if (res.code == 0) {
						this.bannerList = res.data.goods_banner
						this.detail = res.data
						this.detail.goods_details = this.handleImgHtml(res.data.goods_details)
					}
				})
		},
		handleImgHtml(str) {
			var richtext = str
			const regex = new RegExp('<img', 'gi');
			richtext = richtext.replace(regex, `<img style="max-width: 100%;margin: 0px;padding:0px;vertical-align: top;"`);
			return richtext;
		},
		confrimOrderPage(isCart) {
			this.confirmModal = true
			this.cartOrOrder = isCart
			// const shopItem = {
			//   goods_id: this.id,
			//   goods_price: this.detail.goods_price,
			//   goods_name: this.detail.goods_name,
			//   goods_cover_img: this.detail.goods_cover_img,
			//   is_cash_back: this.is_cash_back,
			//   shopNum: this.shopNum,
			//   cart_id: ''
			// }
			// let shopList = []
			// shopList.push(shopItem)
			// uni.setStorageSync('shopList', shopList)
			// uni.navigateTo({
			//   url: '/pages/confirm-order/confirm-order'
			// });
		}
	}
}
